import service from 'utils/request'

import API from './public'

/* 数据源列表 */
const BaseUrl = '/dsm'

export default {
    // 数据库列表
    _PublicDataBaseList(data) {
        return service({
            url: BaseUrl + '/dict',
            method: 'get',
            data
        })
    },
    /* ******************************* */
    // 获取数据源列表
    DSMCList(data) {
        return service({
            url: BaseUrl + '/ds/page',
            method: 'get',
            data
        })
    },
    // 新增数据源
    DSMCAdd(data) {
        return service({
            url: BaseUrl + '/ds',
            method: 'post',
            data
        })
    },
    // 删除数据源
    DSMCDel(data) {
        return service({
            url: BaseUrl + '/ds',
            method: 'DELETE',
            data
        })
    },
    // 更新
    DSMCUpdate(data) {
        return service({
            url: BaseUrl + '/ds',
            method: 'put',
            data
        })
    }
}


